<template>
  <div class="Gallary-container" @click="handleHiddenClick">
    <swiper :options="swiperOptions">
      <swiper-slide v-for="img in imgs" :key="img">
        <img :src="img" alt="" class="scroll-img" />
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: "Gallary",
  data() {
    return {
      swiperOptions: {
        pagination: ".swiper-pagination",
        paginationType: "fraction",
      },
    };
  },
  props: {
    imgs: {
      type: Array,
      required: true,
    },
    isShow: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    handleHiddenClick() {
      this.$emit("close");
    },
  },
};
</script>

<style lang="scss" scoped>
.Gallary-container /deep/ .swiper-container {
  overflow: inherit;
}
.Gallary-container {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: fixed;
  z-index: 2;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: #000;
  color: #fff;
  .scroll-img {
    width: 375px;
  }
  .swiper-container {
    width: 100%;
  }
  .swiper-pagination {
    bottom: -25px;
  }
}
</style>
